#filter *:first-child {
  margin-left: 10px;
}
#filter *:nth-child(2) {
  margin: 10px;
  margin-right: 31px;
}
#filter *:nth-child(3), #filter *:nth-child(4) {
  width: 100px;
  height: 25px;
  margin: 10px;
  margin-left: 0;
  margin-bottom: 0;
}
#filter *:last-child {
  display: block;
  margin: 10px;
  height: 21px;
  width: 370px
}



