table {
  margin: 10px;
  border-collapse: collapse;
}
td, th {
  font: 16px Arial;
  height: 20px;
  border: 1px solid #000;
}
th:first-child {
  width: 60px;
}
th:nth-child(2) {
  width: 150px;
}
th:nth-child(3) {
  width: 80px;
}
th:last-child {
  width: 93px;
}
th>div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
th>div>div:first-child {
  margin:auto;
}
.up, .down {
  display: block;
  font: 4px Arial;
}
td:first-child {
  text-align: center;
}
#description {
  position: fixed;
  display: inline-block;
  border-radius: 3px;
  background: #eee;
  padding: 5px;
  word-wrap: break-word;
  max-width: 340px;
}



