body {
  margin: 0;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}
#todo {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
form {
  margin: 10px 10px 0 10px;
  -ms-flex-item-align: start;
      align-self: flex-start;
  border: 1px solid #000;
  border-radius: 3px;
}
#add * {
  display: block;
  margin: 10px;
}
#add *:first-child {
  height: 20px;
  display: inline-block;
  width: 200px;
}
#add *:nth-child(2), #add *:nth-child(3) {
  height: 26px;
  display: inline-block;
  margin-left: 0;
  width: 50px;
}
#add *:nth-child(3) {
  width: 100px;
}
#add *:nth-child(4) {
  margin-top: 0;
  width: 368px;
  height: 100px
}
#add *:last-child {
  margin-left: auto;
  margin-right: 10px;
}



